

$width: if(condition, calc(100% - 11%), calc(100% - 95px));

.mounthMathElement {
	width: 80%;
	display: inline-grid;
	min-height: 61px;
	align-items: center;
	justify-items: start;
	align-content: space-around;
}
.inputsMayChenge {
	display: flex;
	width: 100%;
	justify-content: space-evenly;
	align-content: center;
	align-items: center;
	p {
		width: 40%;
		margin: 0.1rem;
		font-family: cursive;
		font-size: 1.1rem;
		text-align: center;
	}
}

@media screen and (max-width: 550px) {
	.mounthMathElement {
		width: 100%;
	}
}
