.mailTo {
	position: relative;
	color: #ffeb3b;
	text-decoration: none;
	font-weight: bold;
}

.mailTo::after {
	content: "";
	position: absolute;
	bottom: -2px;
	left: 0;
	width: 100%;
	height: 2px;
	background-color: transparent;
	transition: background-color 0.3s ease;
}

.mailTo:hover::after,
.mailTo:active::after {
	background-color: #3498db;
	animation: underline 2s infinite linear;
}

@keyframes underline {
	0% {
		transform: translateX(-100%);
	}
	50% {
		transform: translateX(100%);
	}
	100% {
		transform: translateX(-100%);
	}
}
