@import "https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300";
@import url("./normalization.module.scss");

button {
	// Сброс всех стилей кнопки
	all: unset;
	&:focus-visible {
		outline: none; // Убираем стандартное выделение при фокусе
		box-shadow: 0 0 0 2px #3498db; // Пример тени при фокусе
	  }
}

div[class*="box"] {
	// height: 33.33%;
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}

.box1 {
	background-color: #ff6766;
}
.box2 {
	background-color: #3F51B5;
}

.box3 {
	background-color: #2e4548;
}

.btn {
	// line-height: 50px;
	// height: 50px;
	text-align: center;
	width: 250px;
	cursor: pointer;
}

.btnOne {
	color: #fff;
	transition: all 0.3s;
	position: relative;
}
.btnOne span {
	transition: all 0.3s;
}
.btnOne::before {
	content: "";
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1;
	opacity: 0;
	transition: all 0.3s;
	border-top-width: 1px;
	border-bottom-width: 1px;
	border-top-style: solid;
	border-bottom-style: solid;
	border-top-color: rgba(255, 255, 255, 0.5);
	border-bottom-color: rgba(255, 255, 255, 0.5);
	transform: scale(0.1, 1);
}
.btnOne:hover span {
	letter-spacing: 2px;
}
.btnOne:hover::before {
	opacity: 1;
	transform: scale(1, 1);
}
.btnOne::after {
	content: "";
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1;
	transition: all 0.3s;
	background-color: rgba(255, 255, 255, 0.1);
}
.btnOne:hover::after {
	opacity: 0;
	transform: scale(0.1, 1);
}

.btnThree {
	color: #fff;
	transition: all 0.5s;
	position: relative;
}
.btnThree::before {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1;
	background-color: rgba(255, 255, 255, 0.1);
	transition: all 0.3s;
}
.btnThree:hover::before {
	opacity: 0;
	transform: scale(0.5, 0.5);
}
.btnThree::after {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1;
	opacity: 0;
	transition: all 0.3s;
	border: 1px solid rgba(255, 255, 255, 0.5);
	transform: scale(1.2, 1.2);
}
.btnThree:hover::after {
	opacity: 1;
	transform: scale(1, 1);
}
