.dateSelection {
	margin: 0.1rem;
	min-width: 97px;
	width: 10%;
	display: inline-flex;
	float: right;
}
.dateForm {
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}
.selects {
	border-radius: 0.3rem;
}
.selectedMonth {
	margin: 0 0.1rem 0 0;
}
.selectedYear {
	margin: 0 0 0 0.1rem;
}
option:nth-child(2n) {
	background-color: #e9e8e8;
}

option {
	text-align: center;
}
@media screen and (max-width: 550px) {
	.dateSelection {
		width: 100%;
	}

	.selects {
		width: 40%;
		font-size: 1.3rem;
	}
}
